		onLoad(option){
			console.log(option)
			if(option.self){
				this.addrList=JSON.parse(option.self)
				if(this.addrList.distance){
					this.getgoods_name=true;
				}
			}
			
			
			// 获取购买商品信息
			
			if(option.iscart==0){
				this.goodsinfo.goods_spec=option.size;
				
				this.goodsinfo.goods_number=option.num;
				this.goodsinfo.goods_id=option.id
				this.goods.push(this.goodsinfo)
			}
		},
		onBackPress() {
			//页面后退时候，清除订单信息
			this.clearOrder();
		},
		filters: {
			toFixed: function(x) {
				return parseFloat(x).toFixed(2);
			}
		},
		methods: {
			//选取送货方式
			sendType(){
				uni.navigateTo({
					url:"/pages/sendType/sendType"
				})
			},
			// 获取购买商品信息
		
			clearOrder() {
				uni.removeStorage({
					key: 'buylist',
					success: (res) => {
						this.buylist = [];
						//console.log('remove buylist success');
					}
				});
			},
			toPay() {
				//商品列表
				let paymentOrder = [];
				let goodsid = [];
				let len = this.buylist.length;
				for (let i = 0; i < len; i++) {
					paymentOrder.push(this.buylist[i]);
					goodsid.push(this.buylist[i].id);
				}
				if (paymentOrder.length == 0) {
					uni.showToast({
						title: '订单信息有误，请重新购买',
						icon: 'none'
					});
					return;
				}
				//本地模拟订单提交UI效果
				uni.showLoading({
					title: '正在提交订单...'
				})
				setTimeout(() => {
					uni.setStorage({
						key: 'paymentOrder',
						data: paymentOrder,
						success: () => {
							uni.hideLoading();
							uni.redirectTo({
								url: "../pay/payment/payment?amount=" + this.sumPrice
							})
						}
					})
				}, 1000)

			}
		
		}
